    ,mounted(){
      //this.showLoading();
      var host =  window.location.href ;
      var _self = this;
      if(host.indexOf("?") != -1){
        var url = host;
        var key = url.split('?')[1].split('&')
        var u = {}
        for (var i = 0; i < key.length; i++) {
          var urls = key[i].split('=')
          var f = urls[0]
          u[f] = urls[1]
        }
        console.log(u) // object{key0:'0', key1:'1', key2:'2'}
      }
      var code = u && u.code ? u.code : 0 ;
      var user = M.F.D.get("user");
      var role = M.F.D.get("role")

      console.log("code",code,"role",role,"u",u );

       this.H.toLoginByCode(code)
      .then(function(){  // 成功
        var path = M.F.D.get("pathname");
       // var q = M.F.D.get("query");
        //alert(path);
          if( M.user.isBindMobile){
            M.checkPath();
          // login 里面有处理
          }else if( !M.user.isBindMobile){
            _self.$router.push("/admin/Login");
          // login 里面有处理
          }



      },function(msg){ // 失败
      alert("error");
      return;
        _self.H.toOauth();
        _self.$emit("modalAlert",msg);
      }).catch(function(err){
      });
    }
    ,watch:{


    }
}



